import React, { Component } from "react";
import { Link } from "react-router-dom";
import Auxi from "../auxi";
import GameBoy from "../../containers/gameboy/gameboy";
import styles from "./welcome.module.css";

class Welcome extends Component {
  render() {
    return (
      <Auxi>
        <GameBoy>
        <div className={styles.Container}>
          <div className={styles.Selection}>PLEASE SELECT GAME</div>
          <Link to="/glx" className={styles.Link}>
           Galaxian
          </Link>
          <Link to="/xxx" className={styles.Link}>
           In development
          </Link>
        </div>
       </GameBoy>
      </Auxi>
    );
  }
}

export default Welcome;
