import React, { Component } from 'react';
import Auxi from '../../auxi/auxi';
import InGame from './glx';
import Logo from '../../components/glx/logo/logo';
import BackGround from '../../components/glx/background/background';
import GameBoy from '../../containers/gameboy/gameboy';
import { connect } from 'react-redux';

let GAMESTATUS = false;

class GlxStart extends Component {
  startGameHandler = (e) => {
    if (e.code === 'Enter') {
      GAMESTATUS = true;
    }
  };

  render() {
    if (!GAMESTATUS) {
      return (
        <Auxi>
          <GameBoy>
            <BackGround
              width={this.props.width}
              height={this.props.height}
              y={this.props.bgy}
              bgmove={this.props.bgmove}
              score={this.props.score}
            />
            <Logo
              logoy={this.props.logoy}
              width={this.props.width}
              height={this.props.height}
              keyDown={this.startGameHandler}
              logomove={this.props.logomove}
            />
          </GameBoy>
        </Auxi>
      );
    }
    if (GAMESTATUS) {
      return (
        <Auxi>
          <GameBoy>
            <BackGround
              width={this.props.width}
              height={this.props.height}
              //bg vertial: fixed = 0
              x={0}
              y={this.props.bgy}
              bgmove={this.props.bgmove}
              score={this.props.score}
            />
            <InGame width={this.props.width} height={this.props.height} />
          </GameBoy>
        </Auxi>
      );
    }
  }
}

const mapStateToProps = (state) => {
  return {
    bgy: state.bgReducer.bgy,
    logoy: state.logoReducer.logoy,
    score: state.scoreReducer.score,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    bgmove: () => {
      dispatch({ type: 'bg' });
    },
    logomove: () => {
      dispatch({ type: 'logo' });
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(GlxStart);
