import React, { Component } from "react";
import Background from "../../components/gameboy/background/background";
import Panel from "../../components/gameboy/panel/panel";
import Screen from "../../components/gameboy/screen/screen";
import Auxi from "../../auxi/auxi";

class GameBoy extends Component {
  render() {
   
    return (
      <Auxi>
        <Background>
          <Screen>{this.props.children}</Screen>
          <Panel />
        </Background>
      </Auxi>
    );
  }
}

export default GameBoy;
