const initialState = {
  score: 0,
};

const scoreReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GREEN":
      return { score: state.score + action.num };
    case "PURPLE":
      return { score: state.score + action.num };
    case "RED":
      return { score: state.score + action.num };
    case "BOSS":
      return { score: state.score + action.num };
    default:
      return state;
  }
};

export default scoreReducer;
