const bxS1 = Math.random() + 2;
const bxS2 = Math.random() + 2;
const bxS3 = Math.random() + 2;
const bxS4 = Math.random() + 2;
const bxS5 = Math.random() + 2;

const mapArr = (state, action) => {
  const target = [...state.enemy];

  action.arr.forEach((el) => {
    switch (el <= 22) {
      case true:
        switch (target[el].AP) {
          case 1:
            target[el] = {
              key: el,
              x: target[el].x,
              y: target[el].y,
              status: "ATTACKING",
              angle: target[el].angle - (3 * Math.PI) / 180,
              AP: target[el].angle > -1.25 * Math.PI ? 1 : 2,
              dummyy: target[el].dummyy,
              dummyx: target[el].dummyx,
              color: target[el].color,
              b1: target[el].b1,
            };
            break;

          case 2:
            target[el] = {
              key: el,
              x: target[el].x + 4 + (action.shipx - target[el].x) / 100,
              y: target[el].y + 1,
              status: "ATTACKING",
              angle: target[el].angle,
              AP:
                target[el].x - 40 + 40 * Math.cos(target[el].angle) >
                action.shipx // enemyx > shipx
                  ? 3
                  : 2,
              dummyy: target[el].dummyy,
              dummyx: target[el].dummyx,
              color: target[el].color,
              b1: target[el].b1,
            };
            break;

          case 3:
            target[el] = {
              key: el,
              x:
                target[el].y > 400
                  ? target[el].dummyx
                  : target[el].x + 3 + (action.shipx - target[el].x) / 100,
              y: target[el].y > 400 ? 0 : target[el].y + 1.5,
              status: "ATTACKING",
              angle:
                target[el].y > 400 ? 0 : target[el].angle - (1 / 80) * Math.PI,
              AP: target[el].y > 400 ? 4 : 3,
              dummyy: target[el].dummyy,
              dummyx: target[el].dummyx,
              color: target[el].color,
              b1: target[el].b1,
            };
            break;

          case 4:
            target[el] = {
              key: el,
              x: target[el].dummyx,
              y:
                target[el].y === target[el].dummyy
                  ? target[el].y
                  : target[el].y + 1,
              status:
                target[el].y === target[el].dummyy ? "MOVING" : "ATTACKING",
              angle: 0,
              AP: 4,
              dummyy: target[el].dummyy,
              dummyx: target[el].dummyx,
              color: target[el].color,
              b1: target[el].b1,
            };
            break;
          default:
        }
        break;
      default:
    }
  });
  return target;
};

export default (state, action) => {
  return { enemy: mapArr(state, action) };
};
