const initialState = {
  shipx: 285,
  shipStatus: true,
};

const shipReducer = (state = initialState, action) => {
  switch (action.type) {
    case "LEFT":
      return {
        shipx: state.shipx <= 0 ? 0 : state.shipx - 2,
        shipStatus: state.shipStatus,
      };
    case "RIGHT":
      return {
        shipx: state.shipx >= 570 ? 570 : state.shipx + 2,
        shipStatus: state.shipStatus,
      };
    case "SHIPVANISH":
 
      return { shipx: null, shipStatus: false };
    case "SHIPRESUME":
      return { shipx: 285, shipStatus: true };

    default:
      return state;
  }
};

export default shipReducer;
