import React, { PureComponent } from "react";
import green2img from "../../../assets/images/green2.jpg";
import purple2img from "../../../assets/images/purple2.jpg";
import red2img from "../../../assets/images/red2.jpg";
import bossimg from "../../../assets/images/boss.jpg";
import select from "../../../utility/select";
import styles from "./enemyattack.module.css";



class EnemyAttack extends PureComponent {
  constructor(props) {
    super(props);
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidUpdate() {
    this.paint();
  }

  componentDidMount() {
    this.pickAttackerHandler();
    this.enemyAttackLoop();
  }

  pickAttackerHandler = () => {
     
    const INTERVAL = Math.floor(Math.random() * 8000);
    const id = this.props.enemies2[0].key;
    const cord = { x: this.props.enemies2[0].x, y: this.props.enemies2[0].y };
    this.props.pickAttacker(id, cord);
    setTimeout(this.pickAttackerHandler, INTERVAL);
  };

  enemyAttackLoop = () => {
    this.props.enemyAttack(
      this.props.enemies.map((el) => el.key),
      this.props.shipx
    );
    this.props.fire(this.props.bu.map((el) => el.key));

    requestAnimationFrame(this.enemyAttackLoop);
  };

  paint() {
    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");
      const greenimage = new Image();
      greenimage.src = green2img;
      const purpleimage = new Image();
      purpleimage.src = purple2img;
      const redimage = new Image();
      redimage.src = red2img;
      const bossimage = new Image();
      bossimage.src = bossimg;
      greenimage.onload = () => {
        const R = 40;
        context.clearRect(0, 0, 600, 400);
        this.props.enemies.forEach((enemy) => {
          const x = enemy.x - R + R * Math.cos(enemy.angle);
          const y = enemy.y + R * Math.sin(enemy.angle);
          const ALPHA = Math.atan((this.props.shipx - x) / (400 - y));
          switch (enemy.AP) {
            case 1:
            case 4:
              context.save();
              context.translate(x, y);
              context.rotate(enemy.angle); // rotate around that point
              context.drawImage(
                enemy.color === 1
                  ? greenimage
                  : enemy.color === 2
                  ? purpleimage
                  : enemy.color === 3
                  ? redimage
                  : bossimage,
                0,
                0
              );
              context.restore();
              break;
            case 2:
              context.save();
              context.translate(x, y);
              context.rotate(Math.PI - ALPHA); // rotate around that point
              context.drawImage(
                enemy.color === 1
                  ? greenimage
                  : enemy.color === 2
                  ? purpleimage
                  : enemy.color === 3
                  ? redimage
                  : bossimage,
                0,
                0
              );
              context.restore();
              break;
            case 3:
              context.save();
              context.translate(x, y);
              context.rotate(Math.PI - ALPHA); // rotate around that point
              context.drawImage(
                enemy.color === 1
                  ? greenimage
                  : enemy.color === 2
                  ? purpleimage
                  : enemy.color === 3
                  ? redimage
                  : bossimage,
                0,
                0
              );
              context.restore();
          }
        });
        
        this.props.bu.forEach((b) => {
          if (b.color === 1 && b.b1.y >= 200) {
            if (b.b1) {
              context.fillStyle = "white";
              context.fillRect(b.b1.x, b.b1.y, 2, 6);
            }
          }
          if (b.color === 2 && b.b1.y >= 180) {
            if (b.b1) {
              context.fillStyle = "white";
              context.fillRect(b.b1.x, b.b1.y, 2, 6);
            }
          }
          if (b.color === 3 && b.b1.y >= 160) {
            if (b.b1) {
              context.fillStyle = "white";
              context.fillRect(b.b1.x, b.b1.y, 2, 6);
            }
          }
          if (b.color === 4 && b.b1.y >= 140) {
            if (b.b1) {
              context.fillStyle = "white";
              context.fillRect(b.b1.x, b.b1.y, 2, 6);
            }
          }
        });
      };
    }
  }

  render() {
    const { width, height } = this.props;
    return (
      <canvas
        id={"enemyattack"}
        ref={this.canvasRef}
        width={width}
        height={height}
       className={styles.Canvas}
      />
    );
  }
}
export default EnemyAttack;
