import { combineReducers } from "redux";
import bgReducer from "./reducers/glxn/background";
import logoReducer from "./reducers/glxn/logo";
import shipReducer from "./reducers/glxn/ship";
import bulletReducer from "./reducers/glxn/bullet";
import enemyReducer from "./reducers/glxn/enemy/enemy";
/*import enemyBulletReducer from "./reducers/glxn/enemyBullet/enemyBullet";
deleted. combined in to enemy*/
import scoreReducer from "./reducers/glxn/score";

const reducer = combineReducers({
   bgReducer,
   logoReducer,
   shipReducer,
   bulletReducer,
   enemyReducer,
   scoreReducer,
});


console.dir(reducer);
export default reducer;
