import eis from "./const";
import move from "./move";
import attack from "./attack";
import explode from "./explode";
import fire from "./fire";

const initialState = eis;

const enemyReducer = (state = initialState, action) => {
  const id = action.id;

  switch (action.type) {
    case "ENEMYMOVE":
      return move(state, action);
    case "SELECT":
      return {
        enemy: (function () {
          let target = [...state.enemy];
          target[id] = {
            key: target[id].key,
            x: target[id].x,
            y: target[id].y,
            status: "ATTACKING", //stop moving
            angle: 0,
            AP: 1, // enter attacking phase
            dummyy: target[id].y, //record current y, keep moving
            dummyx: target[id].x, //record current x, keep moving
            color: target[id].color,
            b1: { x: action.cord.x - 200, y: action.cord.y - 90 },
          };
          return target;
        })(),
      };
    case "ENEMYATTACK":
      return attack(state, action);
    // case "ENEMYEXPLODE":
    //   return explode(state,action)
    case "FIRE":
      return fire(state, action);
    case "CANCELFIRE":
      return {
        enemy: (function () {
          let target = [...state.enemy];
          target[id] = {
            key: id,
            x: target[id].x,
            y: target[id].y,
            status: target[id].status,
            angle: target[id].angle,
            AP: target[id].AP,
            dummyy: target[id].dummyy,
            dummyx: target[id].dummyx,
            b1: null,
            color: target[id].color,
          };
          return target;
        })(),
      };
    case "VANISH":
 
      return {
        enemy: (function () {
          let target = [...state.enemy];
          target[id] = {
            key: id,
            x: target[id].x,
            y: target[id].y,
            status: null,
            angle: target[id].angle,
            AP: target[id].AP,
            dummyy: target[id].dummyy,
            dummyx: target[id].dummyx,
            b1: target[id].b1,
            color: target[id].color,
          };
          return target;
        })(),
      };
    default:
      return state;
  }
};

export default enemyReducer;
