let counter = 0;
let direction = "RIGHT";
const S = 5;

const dummyMove = (state, dir) => {
  const target = [...state.enemy];
  target.forEach((el, id) => {
    target[id] = target[id].status //attacking moving null
      ? {
          key: target[id].key,
          x: target[id].x,
          y: target[id].y,
          status: target[id].status,
          angle: target[id].angle,
          AP: target[id].AP,
          dummyy: target[id].dummyy,
          dummyx:
            dir === "RIGHT" ? target[id].dummyx + S : target[id].dummyx - S,
          color: target[id].color,
          b1: target[id].b1,
        }
      : {
          key: target[id].key,
          x: target[id].x,
          y: target[id].y,
          status: null,
          angle: target[id].angle,
          AP: target[id].AP,
          dummyy: target[id].dummyy,
          dummyx: target[id].dummyx,
          color: target[id].color,
          b1: target[id].b1,
        };
  });

  return target;
};

const mapArr = (state, action, dir) => {
  const target = dummyMove(state, dir);
  action.arr.forEach((el) => {
    target[el] = {
      key: target[el].key,
      x: dir === "RIGHT" ? target[el].x + S : target[el].x - S,
      y: target[el].y,
      status: target[el].status,
      angle: target[el].angle,
      AP: target[el].AP,
      dummyy: null,
      dummyx: null,
      color: target[el].color,
      b1: target[el].b1,
    };
  });
  return target;
};

export default (state, action) => {
  let target;
  target = {
    enemy: mapArr(state, action, direction),
  };
  if (direction === "RIGHT") {
    counter++;
  }
  if (direction === "LEFT") {
    counter--;
  }
  if (counter === 12) {
    direction = "LEFT";
  }
  if (counter === 0) {
    direction = "RIGHT";
  }
  return target;
};
