import React, { PureComponent } from "react";
import shipimg from "../../../assets/images/ship.jpg";
import se4 from "../../../assets/images/se4.jpg";
import se3 from "../../../assets/images/se3.jpg";
import se2 from "../../../assets/images/se2.jpg";
import se1 from "../../../assets/images/se1.jpg";
import styles from "./ship.module.css";

class Ship extends PureComponent {
  constructor(props) {
    super(props);
    this.keyState = {};
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    window.addEventListener("keydown", (e) => {
      this.keyState[e.code] = true;
    });
    window.addEventListener("keyup", (e) => {
      this.keyState[e.code] = false;
    });
    this.shipLoop();
    this.paint();
    this.collisionDetectBu();
  }

  componentDidUpdate() {
    this.paint();
  }

  componentWillUnmount() {
    this.aniStatus = false;
  }

  collisionDetectBu = () => {
    this.props.bu.forEach((b) => {

      if (
        b.b1.y + 7 > 360 &&
        b.b1.y + 7 < 380 &&
        this.props.shipx < b.b1.x &&
        this.props.shipx > b.b1.x - 35
      ) {
        // &&enemy.status === 1)
        // this.props.explode(id);
        this.explodingEffect();
         
        this.props.cancelEnemyBullet(b.key);
        this.props.shipVanish();

        setTimeout(this.props.shipResume, 3000);
      }
    });
    requestAnimationFrame(this.collisionDetectBu);
  };

  shipLoop = () => {
    if (this.keyState["ArrowLeft"] || this.keyState["KeyA"]) {
      this.props.shipMove("LEFT");
    }
    if (this.keyState["ArrowRight"] || this.keyState["KeyD"]) {
      this.props.shipMove("RIGHT");
    }
    if (this.aniStatus) {
      requestAnimationFrame(this.shipLoop);
    }
  };

  explodingEffect = () => {
    this.explode1 = { x: this.props.shipx };
    setTimeout(() => {
      this.explode2 = { ...this.explode1 };
      setTimeout(() => {
        this.explode3 = { ...this.explode2 };
        setTimeout(() => {
          this.explode4 = { ...this.explode3 };
          setTimeout(() => {
            this.explode4 = null;
          }, 50);
          this.explode3 = null;
        }, 50);
        this.explode2 = null;
      }, 50);
      this.explode1 = null;
    }, 50);
  };

  paint = () => {
    const { shipx, shipStatus } = this.props;
    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");
      const shipimage = new Image();
      shipimage.src = shipimg;
      const se4image = new Image();
      se4image.src = se4;
      const se3image = new Image();
      se3image.src = se3;
      const se2image = new Image();
      se2image.src = se2;
      const se1image = new Image();
      se1image.src = se1;
      shipimage.onload = () => {
        context.clearRect(0, 0, 600, 400);
        if (shipStatus) {
          context.drawImage(shipimage, shipx, 360, 30, 28);
        }
      };
      if (this.explode1) {
        context.drawImage(se1image, this.explode1.x, 360);
        requestAnimationFrame(this.paint);
      }
      if (this.explode2) {
        context.drawImage(se2image, this.explode2.x, 359);
        requestAnimationFrame(this.paint);
      }
      if (this.explode3) {
        context.drawImage(se3image, this.explode3.x, 358);
        requestAnimationFrame(this.paint);
      }
      if (this.explode4) {
        context.drawImage(se4image, this.explode4.x, 355);
        requestAnimationFrame(this.paint);
      }
    }
  };

  render() {
    return (
      <canvas
        id={"ship"}
        ref={this.canvasRef}
        width={this.props.width}
        height={this.props.height}
       className={styles.Canvas}
      />
    );
  }
}

export default Ship;
