import React, { PureComponent } from 'react';
import Auxi from '../../auxi/auxi';
import { connect } from 'react-redux';
import Ship from '../../components/glx/ship/ship';
import Bullet from '../../components/glx/bullet/bullet';
import Enemy from '../../components/glx/enemy/enemy';
import EnemyAttack from '../../components/glx/enemyattack/enemyattack';
import music from '../../utility/music';

class InGame extends PureComponent {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    music.start();
  }
  componentDidUpdate() {}
  render() {
    const MOVARR = this.props.enemy.filter((el) => {
      if (el) {
        return el.status === 'MOVING';
      }
    });

    const BUARR = this.props.enemy.filter((el) => {
      if (el) {
        return el.b1 !== null;
      }
    });

    const ATKARR = this.props.enemy.filter((el) => {
      if (el) {
        return el.status === 'ATTACKING';
      }
    });

    return (
      <Auxi>
        <Ship
          width={this.props.width}
          height={this.props.height}
          shipx={this.props.shipx}
          shipMove={this.props.shipMove}
          bu={BUARR}
          cancelEnemyBullet={this.props.cancelEnemyBullet}
          shipVanish={this.props.shipVanish}
          shipResume={this.props.shipResume}
          shipStatus={this.props.shipStatus}
        />
        <Bullet
          width={this.props.width}
          height={this.props.height}
          bullety={this.props.bullety}
          attack={this.props.attack}
          shipx={this.props.shipx}
          enemies2={MOVARR}
          vanish={this.props.vanish}
          explode={this.props.explode}
          bulletStatus={this.props.bulletStatus}
          passId={this.passId}
          YB={60}
          YD={25}
          enemies={ATKARR}
          goal={this.props.goal}
          enemyExplode={this.props.enemyExplode}
          cancelEnemyBullet={this.props.cancelEnemyBullet}
          shipStatus={this.props.shipStatus}
        />
        <Enemy
          width={this.props.width}
          height={this.props.height}
          XB={70}
          XD={40}
          YB={60}
          YD={25}
          S={5}
          enemies={MOVARR}
          enemyMove={this.props.enemyMove}
        />
        <EnemyAttack
          width={this.props.width}
          height={this.props.height}
          XB={70}
          XD={40}
          YB={60}
          YD={25}
          S={5}
          enemies={ATKARR}
          enemies2={MOVARR}
          enemyAttack={this.props.enemyAttack}
          pickAttacker={this.props.pickAttacker}
          shipx={this.props.shipx}
          bu={BUARR}
          fire={this.props.fire}
        />
      </Auxi>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    shipx: state.shipReducer.shipx,
    shipStatus: state.shipReducer.shipStatus,
    bullety: state.bulletReducer.bullety,
    bulletStatus: state.bulletReducer.bulletStatus,
    enemy: state.enemyReducer.enemy,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    attack: (shipx) => {
      dispatch({ type: 'ATTACK', shipx: shipx });
    },
    shipMove: (DIR) => {
      dispatch({ type: DIR });
    },
    enemyMove: (arr) => {
      dispatch({ type: 'ENEMYMOVE', arr: arr });
    },

    vanish: (id) => {
      dispatch({ type: 'VANISH', id: id });
    },
    goal: (color, num) => {
      dispatch({ type: color, num: num });
    },
    enemyAttack: (arr, shipx) => {
      dispatch({
        type: 'ENEMYATTACK',
        arr: arr,
        shipx: shipx,
      });
    },
    pickAttacker: (id, cord) => {
      dispatch({ type: 'SELECT', id: id, cord: cord });
    },
    fire: (arr) => {
      dispatch({ type: 'FIRE', arr: arr });
    },
    cancelEnemyBullet: (id) => {
      dispatch({ type: 'CANCELFIRE', id });
    },
    shipVanish: () => {
      dispatch({ type: 'SHIPVANISH' });
    },
    shipResume: () => {
      dispatch({ type: 'SHIPRESUME' });
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(InGame);
