const mapArr = (state, action) => {
  const target = [...state.enemy];

  action.arr.forEach((el, id) => {
    switch (el <= 22) {
      case true:
        target[el] = {
          key: el,
          x: target[el].x,
          y: target[el].y,
          status: target[el].status,
          angle: target[el].angle,
          AP: target[el].AP,
          dummyy: target[el].dummyy,
          dummyx: target[el].dummyx,
          b1:
            target[el].b1.y > 400
              ? null
              : { x: target[el].b1.x + 2, y: target[el].b1.y + 2 },
          color: target[el].color,
        };
    }
  });
  return target;
};

export default (state, action) => {
  return { enemy: mapArr(state, action) };
};
