import React, { Component } from "react";
import styles from "./bullet.module.css";
import music from "../../../utility/music";
import e4 from "../../../assets/images/e4.jpg";
import e3 from "../../../assets/images/e3.jpg";
import e2 from "../../../assets/images/e2.jpg";
import e1 from "../../../assets/images/e1.jpg";

class Bullet extends Component {
  constructor(props) {
    super(props);
    this.keyState = {};
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  shouldComponentUpdate(nextProps) {
    //only redraw if bullet changes instead of enemy

    if (
      this.props.bullety === nextProps.bullety &&
      this.props.shipx === nextProps.shipx
    ) {
      return false;
    } else return true;
  }

  componentDidMount() {
    this.paint();
    window.addEventListener("keydown", this.attack);
  }
  componentDidUpdate() {
    this.paint();
  }

  componentWillUnmount() {
    this.aniStatus = false;
    window.removeEventListener("keydown", this.attack);
  }

  attack = (e) => {
    if (e.code === "Space") {
      music.attack();
      //remember firing x cordinate
      this.bulletx = this.props.shipx + 14;
      window.removeEventListener("keydown", this.attack);
      this.attackLoop();
    }
  };

  movingEnemyCollisionDetect() {
    this.props.enemies2.forEach((enemy) => {
      if (
        this.props.bullety > enemy.y &&
        this.props.bullety < enemy.y + 14 &&
        this.bulletx > enemy.x - 1 &&
        this.bulletx < enemy.x + 27
      ) {
        // &&enemy.status === 1)
        // this.props.explode(id);
        music.ememyboom();
        this.goal(enemy, 30, 40, 50, 60);
        this.props.vanish(enemy.key);
        this.explodingEffect(enemy, "MOV");
      }
    });
  }

  goal = (enemy, a, b, c, d) => {
    switch (enemy.color) {
      case 1:
        this.props.goal("GREEN", a);
        break;
      case 2:
        this.props.goal("PURPLE", b);
        break;
      case 3:
        this.props.goal("RED", c);
        break;
      case 4:
        this.props.goal("BOSS", d);
        break;
      default:
    }
  };

  explodingEffect = (enemy, mode) => {
    const R = 40;
    const x = enemy.x - R + R * Math.cos(enemy.angle);
    const y = enemy.y + R * Math.sin(enemy.angle);
    switch (mode) {
      case "MOV":
        this.explode1 = { id: enemy.key, x: enemy.x, y: enemy.y };
        break;
      case "ATK23":
        this.explode1 = { id: enemy.key, x: x - 25, y: y - 15 };
        break;
      case "ATK14":
        this.explode1 = { id: enemy.key, x: x, y: y };
        break;
    }

    setTimeout(() => {
      this.explode2 = { ...this.explode1 };
      setTimeout(() => {
        this.explode3 = { ...this.explode2 };
        setTimeout(() => {
          this.explode4 = { ...this.explode3 };
          setTimeout(() => {
            this.explode4 = null;
          }, 50);
          this.explode3 = null;
        }, 50);
        this.explode2 = null;
      }, 50);
      this.explode1 = null;
    }, 50);
  };

  attackingEnemyCollisionDetect = () => {
    this.props.enemies.forEach((enemy) => {
      const R = 40;
      const x = enemy.x - R + R * Math.cos(enemy.angle);
      const y = enemy.y + R * Math.sin(enemy.angle);
      if (
        enemy.AP === 1 &&
        this.props.bullety > y - 2 &&
        this.props.bullety < y + 20 &&
        this.bulletx > x - 5 &&
        this.bulletx < x + 27
      ) {
        music.ememyboom();

        this.goal(enemy, 50, 60, 70, 80);
        this.props.vanish(enemy.key);

        this.props.cancelEnemyBullet(enemy.key);
        this.explodingEffect(enemy, "ATK14");
      }
      if (
        (enemy.AP === 2 || enemy.AP === 3) &&
        this.props.bullety > y - 10 &&
        this.props.bullety < y + 20 &&
        this.bulletx > x - 30 &&
        this.bulletx < x + 5
      ) {
        music.ememyboom();
        this.goal(enemy, 50, 60, 70, 80);
        this.props.vanish(enemy.key);
        this.explodingEffect(enemy, "ATK23");
      }
      if (
        enemy.AP === 4 &&
        this.props.bullety > y &&
        this.props.bullety < y + 14 &&
        this.bulletx > x - 1 &&
        this.bulletx < x + 27
      ) {
        music.ememyboom();
        this.goal(enemy, 30, 40, 50, 60);
        this.props.vanish(enemy.key);

        this.explodingEffect(enemy, "ATK14");
      }
    });
  };

  attackLoop = () => {
    //dispatch action
    this.props.attack();
    this.movingEnemyCollisionDetect();
    this.attackingEnemyCollisionDetect();

    if (this.props.bulletStatus) {
      if (this.props.bullety > -12) {
        requestAnimationFrame(this.attackLoop);
      }
      if (this.props.bullety <= -12) {
        this.props.attack();
        window.addEventListener("keydown", this.attack);
      }
    } else {
      window.addEventListener("keydown", this.attack);
    }
  };

  paint = () => {
    const { bullety, shipx, shipStatus } = this.props;
    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext("2d");

      const e4image = new Image();
      e4image.src = e4;
      const e3image = new Image();
      e3image.src = e3;
      const e2image = new Image();
      e2image.src = e2;
      const e1image = new Image();
      e1image.src = e1;
      e1image.onload = () => {
        context.clearRect(0, 0, 600, 400);
        if (shipStatus) {
          context.fillStyle = "rgb(234,158,34)";
          context.fillRect(
            bullety === 353 ? shipx + 14 : this.bulletx,
            bullety,
            2,
            7
          );
        }
        if (this.explode1) {
          context.drawImage(e1image, this.explode1.x, this.explode1.y - 3);
          requestAnimationFrame(this.paint);
        }
        if (this.explode2) {
          context.drawImage(e2image, this.explode2.x, this.explode2.y);
          requestAnimationFrame(this.paint);
        }
        if (this.explode3) {
          context.drawImage(e3image, this.explode3.x - 3, this.explode3.y - 3);
          requestAnimationFrame(this.paint);
        }
        if (this.explode4) {
          context.drawImage(e4image, this.explode4.x - 5, this.explode4.y - 12);
          requestAnimationFrame(this.paint);
        }
      };
    }
    // if (this.aniStatus) {
    //
    // }
  };

  render() {
    const { width, height } = this.props;
    return (
      <canvas
        id={"bullet"}
        ref={this.canvasRef}
        width={width}
        height={height}
        className={styles.Canvas}
      />
    );
  }
}

export default Bullet;
