import React, { Component } from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import GlxStart from '../containers/glx/glxstart';
import XxxStart from '../containers/xxx/xxxstart';
import Welcome from './welcome/welcome';

class App extends Component {
  render() {
    return (
      <BrowserRouter basename=''>
        <Route path='/' exact component={Welcome} />
        <Route
          path='/glx'
          render={(props) => (
            <GlxStart
              {...props}
              width={600}
              height={400}
              enemyXb={70}
              enemyXd={40}
              enemyYb={60}
              enemyYd={25}
              enemySpeed={5}
            />
          )}
        />
        <Route
          path='/xxx'
          // https://ui.dev/react-router-v4-pass-props-to-components/
          render={(props) => (
            <XxxStart
              {...props}
              enemyXb={70}
              enemyXd={40}
              enemyYb={60}
              enemyYd={25}
              enemySpeed={5}
            />
          )}
        />
      </BrowserRouter>
    );
  }
}

export default App;
